.card {
    display: flex;
    width: 272px;
    height: 208px;
    position: relative;
    padding: 0 16px;
    box-sizing: border-box;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.currency {
    display: flex;
    margin: 4px 0;
}

.link {
    text-decoration: none;
    color: #fff;
}
