body {
  margin: 0;
}

.text-shadow {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25), 0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}

.cursor { 
  cursor: pointer;
}

.text-align {
  text-align: center;
}

.link {
  text-decoration: none;
}

.text-color {
  color: #fff;
}

.name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 536px; 
  width: 100%;
}

.color {
  color: rgba(0, 204, 204, 1);
}